body {
  margin: 0;
  padding: 0;
  min-width: $desktopWidthMax;
  font-family: $font-primary;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  color: $color-text-default;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
